.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-basicLayout {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 100%;
  position: relative;
  background-color: var(--body-background);
}
.RA-basicLayout.RA-basicLayout-hideMenu .RA-basicLayout-wrapper {
  padding-left: 0 !important;
}
.RA-basicLayout.RA-basicLayout-inlineMode {
  flex-direction: column;
}
.RA-basicLayout.RA-basicLayout-inlineMode .RA-basicLayout-wrapper {
  flex-direction: row;
  padding-bottom: 28px;
}
.RA-basicLayout.RA-basicLayout-splitMode.RA-basicLayout-fixSiderBar .RA-basicLayout-wrapper,
.RA-basicLayout.RA-basicLayout-inlineMode.RA-basicLayout-fixSiderBar .RA-basicLayout-wrapper {
  padding-left: 255px;
}
.RA-basicLayout.RA-basicLayout-splitMode.RA-basicLayout-fixSiderBar .RA-basicLayout-wrapper.RA-basicLayout-wrapper-collapsed,
.RA-basicLayout.RA-basicLayout-inlineMode.RA-basicLayout-fixSiderBar .RA-basicLayout-wrapper.RA-basicLayout-wrapper-collapsed {
  padding-left: 80px;
}
.RA-basicLayout .RA-basicLayout-wrapper {
  flex: auto;
  width: 100%;
  -webkit-transition: padding 0.3s;
  transition: padding 0.3s;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}
.RA-basicLayout .RA-basicLayout-wrapper .RA-basicLayout-wrapper-content {
  display: flex;
  flex-direction: column;
}
.RA-basicLayout .RA-basicLayout-wrapper-viewMain {
  padding: 24px;
  flex: auto;
  overflow-y: auto;
}
.RA-basicLayout .RA-basicLayout-inlineMode-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--component-background);
  height: 28px;
  min-height: 28px;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.2);
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0.8;
  padding-right: 24px;
}
.RA-basicLayout .RA-basicLayout-inlineMode-footer.RA-basicLayout-inlineMode-footer-dark {
  box-shadow: 0px -4px 40px 0 #000000 !important;
}
.RA-basicLayout .RA-basicLayout-inlineMode-footer .RA-basicLayout-inlineMode-footer-icon {
  position: absolute;
  height: 28px;
  width: 255px;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-transition: width 0.3s, color 0.3s;
  transition: width 0.3s, color 0.3s;
  cursor: pointer;
}
.RA-basicLayout .RA-basicLayout-inlineMode-footer .RA-basicLayout-inlineMode-footer-icon:hover {
  color: var(--primary);
}
.RA-basicLayout .RA-basicLayout-inlineMode-footer .RA-basicLayout-inlineMode-footer-icon.RA-basicLayout-inlineMode-footer-collapsed {
  width: 80px;
}
.RA-basicLayout-horizontal {
  display: flex;
  flex-flow: column nowrap;
  background-color: var(--body-background);
  min-height: 100%;
}
.RA-basicLayout-horizontal.RA-basicLayout-horizontal-hideHeader .RA-basicLayout-horizontal-wrapper {
  padding-top: 24px !important;
}
.RA-basicLayout-horizontal.RA-basicLayout-horizontal-fixHeader .RA-header.RA-header-horizontal {
  position: fixed;
}
.RA-basicLayout-horizontal.RA-basicLayout-horizontal-fixHeader .RA-basicLayout-horizontal-wrapper {
  padding-top: 84px;
}
.RA-basicLayout-horizontal.RA-basicLayout-horizontal-contentFlow .RA-header.RA-header-horizontal {
  padding-left: 24px;
}
.RA-basicLayout-horizontal.RA-basicLayout-horizontal-contentFlow .RA-basicLayout-wrapper-viewMain {
  max-width: unset !important;
}
.RA-basicLayout-horizontal .RA-basicLayout-horizontal-wrapper {
  padding: 24px;
}
.RA-basicLayout-horizontal .RA-basicLayout-horizontal-wrapper .RA-basicLayout-wrapper-viewMain {
  margin: 0 auto;
  max-width: 1250px;
}
